<template>

</template>

<script>
    export default {
        name: "BaseTableView",
        data() {
            return {
                defaultRowWidth: "200",//
                tableIsLoading: false,//
                list: [],//
                //
                queryModel: {
                    isPage: true,
                    keyword: null,
                    page: 1,
                    pageSize: 20
                },
            }
        },
        methods: {
            //
            handleSizeChange(val) {
                this.queryModel.pageSize = val;
                this.getList();
            },

            //
            handleCurrentChange(val) {
                this.queryModel.page = val;
                this.getList();
            },
            //
            getHeaderStyle() {
                return {
                    background: "#fafafa",
                    color: 'black'
                }
            },
            //
            remove(id) {
                this.dialogConfirm("", () => {
                    this.request.axiosDeleteData(this, this.module, id, () => {
                        this.getList();
                    });
                });
            }
        },
        watch: {
            //
            "queryModel.keyword": function () {
                this.getList();
            }
        },
        mounted() {
            //
            if (!this.isDialog) {
                this.getList && this.getList();
            }
        }
    }
</script>

